import { DocumentsComponent } from '../../query-by-type/components/documents/documents.component';
import { ApiPaths } from '../../../../shared/enuns/api-paths.enum';
import { EmployeeDetailComponent } from '../../query-by-type/components/employee/employee-detail/employee-detail.component';
import { filterAutorizedItemsMenu } from '../filterAutorizedItemsMenu';

const OPTIONS_MENU = function($this) {
  return [
    {
      id: 'document_default',
      label: 'Documentos',
      command: () => {
        $this.openPopUp('sinsitro_doc');
    }
    },
    {
        id: 'sinistros_post',
        label: 'Novo',
        routerLink: '/register/vehicle-accident'
    },
    {
        id: 'sinistros_patch',
        label: 'Editar',
    },
    {
      id: 'sinistros_delete',
      label: 'Deletar',
      command: () => {
        $this.iMessage = ` do veículo ${$this.data.vehicle.vehicleRegistration}`;
        $this.delete();
     }
    }
  ]
}

const OPTIONS_TAB_COMPONENT_POP_UP = [
  {component: EmployeeDetailComponent, componentKey: 'employee_detail'},
  {component: DocumentsComponent, componentKey: 'employee_doc'}
]

export const EMPLOYEE_VEHICLE_ACCIDENT = function($this){

  return {
            cols: [

                { type:'captalize', field: 'driver', subfield: 'name', header: 'Motorista' },
                {type:'captalize', field: 'type', subfield: 'name',  header: 'Sinistro' },
                { field: 'vehicle', subfield: 'vehicleRegistration', header: 'Veículo Placa' },
                {type:'money', field: 'amount', header: 'Valor' },
                {type:'date', field: 'date', header: 'Data do Sinsitro' },
                {type:'captalize', field: 'byUser', subfield: 'name', header: 'Usuário' },
                {type:'date', field: 'updateAt', header: 'Atualização' }
            ],
                values: [],
                apiKey: ApiPaths.VEHICLE_ACCIDENT,
                title: 'Consultar Sinistros',
                options: filterAutorizedItemsMenu($this, OPTIONS_MENU($this)),
                /* components: OPTIONS_TAB_COMPONENT_POP_UP */
          }
}


