import { CustomerDetailComponent } from './../../query-by-type/components/customer/customerDetail/customerDetail.component';
import { DocumentsComponent } from '../../query-by-type/components/documents/documents.component';
import { ApiPaths } from '../../../../shared/enuns/api-paths.enum';
import { EmployeeDetailComponent } from '../../query-by-type/components/employee/employee-detail/employee-detail.component';
import { filterAutorizedItemsMenu } from '../filterAutorizedItemsMenu';

const OPTIONS_MENU = function($this) {
  return [
    {
      id: 'detail_default',
      label: 'Detalhes',
      command: () => {
        $this.openPopUp('customer_detail');
    }
    },
    {
      id: 'document_default',
      label: 'Documentos',
      command: () => {
        $this.openPopUp('customer_doc');
    }
    },
    {
        id: 'customer_post',
        label: 'Novo',
        routerLink: '/register/customer'
    },
    {
        id: 'customer_patch',
        label: 'Editar',
    },
    {
      id: 'customer_put',
      label: 'Bloquear',
      command: () => {
          $this.iMessage = ` do(a) ${$this.data.name}`;
          $this.block();
      }
    },
    {
      id: 'customer_delete',
      label: 'Deletar',
      command: () => {
        $this.iMessage = ` do(a) ${$this.data.name}`;
        $this.delete();
     }
    }
  ]
}

const OPTIONS_TAB_COMPONENT_POP_UP = [
  {component: CustomerDetailComponent, componentKey: 'customer_detail'},
  {component: DocumentsComponent, componentKey: 'customer_doc'}
]

export const CUSTOMER_TABLE = function($this){

  return {
            cols: [
                { type:'captalize', field: 'name', header: 'Nome' },
                { field: 'taxId',   header: 'CNPJ' },
                { field: 'firstPhone', header: 'Telefone' },
                { field: 'email', header: 'E-mail' },
                { type:'date', field: 'creatAt', header: 'Registrado' },
                { type:'date', field: 'updateAt', header: 'Atualizado' },
            ],
                values: [],
                apiKey: ApiPaths.CUSTOMER,
                title: 'Consultar clientes',
                options: filterAutorizedItemsMenu($this, OPTIONS_MENU($this)),
                components: OPTIONS_TAB_COMPONENT_POP_UP
          }
}


