import { EMPLOYEE_VEHICLE_ACCIDENT } from './table_type/vehicle_accident_table';
import { VEHICLE_ACCIDENT_TYPE_TABLE } from './table_type/vehicle-accident_type_table';
import { SUPPLIER_TABLE } from './table_type/supplier_table';
import { CUSTOMER_TABLE } from './table_type/customer_table';
import { VEIHCLE_TABLE } from './table_type/vehicle_table';
import { EXPENSE_TYPE_TABLE } from './table_type/expense_type';
import { PERFIL_TABLE } from './table_type/perfil_table';
import { EMPLOYEE_TYPE } from './table_type/employe_type_table';
import { EMPLOYEE_TABLE } from './table_type/employe_table';
import { CoreTable } from './coreInterface';

    export const HENDLE_MANEGER = function(value, $this): CoreTable {

      switch (value) {
            case 'employee':
                return EMPLOYEE_TABLE($this);

            case 'employee-office':
                 return EMPLOYEE_TYPE($this);

            case 'perfil':
                  return PERFIL_TABLE($this);

            case 'expense':
                  return EXPENSE_TYPE_TABLE($this);

            case 'vehicle':
                  return VEIHCLE_TABLE($this);

            case 'customer':
                  return CUSTOMER_TABLE($this);

            case 'supplier':
                    return SUPPLIER_TABLE($this);

            case 'vehicle-accident-type':
                      return VEHICLE_ACCIDENT_TYPE_TABLE($this);

            case 'vehicle-accident':
                        return EMPLOYEE_VEHICLE_ACCIDENT($this);
        }
    }

