import { TOAST_SUCCESSO } from './../../../core/menssages/toastEvent';
import { CoreTable } from '../core/coreInterface';
import { HENDLE_MANEGER } from './../core/load-type-table';
import { BreadcrumbService } from './../../../shared/config/app.breadcrumb.service';
import { NgxSpinnerService } from 'ngx-spinner';
import { ElementsResponse, Page } from './../../../shared/model/response/elementsResponse';
import { ApiService } from './../../../core/services/api.service';
import { Component, ComponentFactoryResolver, Inject, OnInit, ViewChild, ViewContainerRef } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { StartPerfil } from 'src/app/startPeril';
import {ConfirmationService} from 'primeng/api';
import { MsgService } from 'src/app/core/menssages/msg.service';
import { BLOCK_SUCESS, DELETE_SUCESS } from 'src/app/core/menssages/menssagens';

@Component({
  selector: 'app-query-by-type',
  templateUrl: './query-by-type.component.html',
  styleUrls: ['./query-by-type.component.scss'],
  providers:[ConfirmationService]
})
export class QueryByTypeComponent implements OnInit {
  public core: CoreTable;
  public pagination = new Page();
  private data: any;
  private iMessage: string = '';
  public dialogDetail: boolean;
  public componentRef: any;

  @ViewChild("container",{read:ViewContainerRef}) container:ViewContainerRef;

  constructor(private router: Router,
              public startPerfil: StartPerfil,
              private service: ApiService,
              private msg: MsgService,
              private componentFactoryResolver:ComponentFactoryResolver,
              private route: ActivatedRoute,
              private spinner: NgxSpinnerService,
              private confirmationService: ConfirmationService,
              private breadcrumbService: BreadcrumbService,
              ) { }

  private block() {
        this.confirmationService.confirm({
            message: `Deseja continuar com o bloqueio${this.iMessage}?`,
            accept: () => {
              this.msg.toast(TOAST_SUCCESSO(BLOCK_SUCESS))
            }
        });
    }

  private delete() {
      this.confirmationService.confirm({
          message: `Deseja continuar com a exclusão${this.iMessage}?`,
          accept: () => {
            this.spinner.show();
            this.service.delete<void>(`${this.core.apiKey}/${this.data.id}`)
                        .subscribe(() => {
                            this.msg.toast(TOAST_SUCCESSO(DELETE_SUCESS));
                            this.spinner.hide();
                            this.load();
                          });
          }
      });
  }

  private openPopUp(value) {
    this.dialogDetail = true;
    this.loadComponet(value);
  }

  public openMenu(event, menu, value){
      menu.toggle(event);
      this.data = value;
  }

  public loadComponet(value): void{
    this.container.clear()
    const component = this.core.components.find(({componentKey}) => componentKey == value )
    this.componentRef = this.componentFactoryResolver.resolveComponentFactory(component.component);
    let compt: any = this.container.createComponent(this.componentRef).instance;
    compt.data = this.data;
  }

  destroyComponent() {
    this.componentRef.destroy();
  }

  public paginate(event) {

    this.service.get(`${this.core.apiKey}`, { page: event.page, size: event.rows })
                .subscribe((r: ElementsResponse) => {
                  this.core.values = r.elements;
                  this.pagination = r.page;
    });
  }

  private load(): any{
    this.spinner.show();
    this.service.get(`${this.core.apiKey}`, { page:  this.pagination.currentPage, size:  this.pagination.size })
                  .subscribe((r: ElementsResponse) => {
                    this.core.values = r.elements;
                    this.pagination = r.page;
                    this.spinner.hide();
                  });
  }

  ngOnInit(): void {

    this.pagination.currentPage = 0;
    this.pagination.size = 10;

    this.route.params.subscribe(({id}) => {
      this.core = HENDLE_MANEGER(id, this);
      this.breadcrumbService.setItems([{ label: `${this.core.title}` }]);
      this.load();
     });
  }
}
