import { VehicleDetailComponent } from './../../query-by-type/components/vehicle/vehicle-detail/vehicle-detail.component';
import { DocumentsComponent } from '../../query-by-type/components/documents/documents.component';
import { ApiPaths } from '../../../../shared/enuns/api-paths.enum';
import { EmployeeDetailComponent } from '../../query-by-type/components/employee/employee-detail/employee-detail.component';
import { filterAutorizedItemsMenu } from '../filterAutorizedItemsMenu';

const OPTIONS_MENU = function($this) {
  return [
    {
      id: 'detail_default',
      label: 'Detalhes',
      command: () => {
        $this.openPopUp('vehicle_detail');
    }
    },
    {
      id: 'document_default',
      label: 'Documentos',
      command: () => {
        $this.openPopUp('component_doc');
    }
    },
    {
        id: 'colaboradores_post',
        label: 'Novo',
        routerLink: '/register/employee'
    },
    {
        id: 'colaboradores_patch',
        label: 'Editar',
    },
    {
      id: 'colaboradores_put',
      label: 'Bloquear',
      command: () => {
          $this.iMessage = ` do(a) ${$this.data.name}`;
          $this.block();
      }
    },
    {
      id: 'colaboradores_delete',
      label: 'Deletar',
      command: () => {
        $this.iMessage = ` do(a) ${$this.data.name}`;
        $this.delete();
     }
    }
  ]
}

const OPTIONS_TAB_COMPONENT_POP_UP = [
  {component: VehicleDetailComponent, componentKey: 'vehicle_detail'},
  {component: DocumentsComponent, componentKey: 'component_doc'}
]

export const VEIHCLE_TABLE = function($this){

  return {
            cols: [
                { type:'captalize', field: 'driverName', header: 'Motorista' },
                { type:'captalize', field: 'driverThirdOrAggregates', header: 'Tipo Motorista' },
                { type:'captalize', field: 'name', header: 'Nome' },
                { type:'captalize', field: 'brand', header: 'Fabricante' },
                { field: 'currentKm', header: 'Km' },
                { field: 'fuelType', header: 'Combustível' },
                { field: 'vehicleRegistration', header: 'Placa' },
                { field: 'chassisNumber', header: 'Chassi' },
                { field: 'consumptionCoefficient', header: 'Coeficiente' },
                { field: 'updateAt', header: 'Atualizado' }
            ],
                values: [],
                apiKey: ApiPaths.VEHICLE,
                title: 'Consultar veículos',
                options: filterAutorizedItemsMenu($this, OPTIONS_MENU($this)),
                components: OPTIONS_TAB_COMPONENT_POP_UP,
                statusColor: true
          }
}


