import { ApiPaths } from '../../../../shared/enuns/api-paths.enum';
import { filterAutorizedItemsMenu } from '../filterAutorizedItemsMenu';

const OPTIONS_MENU = function($this) {
  return [
    {
      id: 'tipo_despesa_delete',
      label: 'Deletar',
      command: () => {
        $this.iMessage = ` do(a) ${$this.data.name}`;
        $this.delete();
     }
    }
  ]
}

export const EXPENSE_TYPE_TABLE = function($this){

  return {
            cols: [
                { type:'captalize', field: 'name', header: 'Tipo' },
                { field: 'description', header: 'Descrição' },
                { type:'date', field: 'creatAt', header: 'Criado' },
                { type:'date', field: 'updateAt', header: 'Atualizado' }
            ],
                values: [],
                apiKey: ApiPaths.TYPE_EXPENSE,
                title: 'Consultar tipos de despesas',
                options: filterAutorizedItemsMenu($this, OPTIONS_MENU($this)),
          }
}


