import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-vehicle-detail',
  templateUrl: './vehicle-detail.component.html',
  styleUrls: ['./vehicle-detail.component.scss'],
})
export class VehicleDetailComponent implements OnInit {
  public data: any;

  constructor() {}

  private buildColor(result): string {
    if (result > 49.9 && result < 64.9) {
      return '#FFC62B';
    }

    if (result >= 71 && result < 84.9) {
      return '#ff812b';
    }

    if (result > 84.9) {
      return 'red';
    }

    return '#2196f3'
  }

  ngOnInit() {
    const list = [];

    if (this.data.controls) {
      this.data.controls.forEach((element) => {
        const calc = this.data.currentKm * 100;
        const result: number = Number((calc / Number(element.km)).toFixed(1));

         const color = this.buildColor(result);

        element = {
          title: element.title,
          description: element.description,
          km: element.km,
          result: result,
          color: color,
        };

        list.push(element);
      });
      this.data.controls = list;
    }
  }
}
