export const filterAutorizedItemsMenu = function ($this, options) {
  const list = []

  setTimeout(() => {
    for(var i=0; i<options.length; i++) {
      if($this.startPerfil.listItemsMenuPerfil.indexOf(options[i].id) > -1 || options[i].id.indexOf('default') > -1 ) {
        list.push(options[i]);
      }
   }
  }, 500);

  return list;
}
