import { QueryByTypeComponent } from './query-by-type.component';
import { Routes, RouterModule } from '@angular/router';
import { NgModule } from '@angular/core';


const routes: Routes = [
  {
        path: '',
        redirectTo: 'type/employee',
      },
  {

 /*
  * TYPES
  * employee
  * employee-office
  * perfil
  * expense
  * vehicle
  * customer,
  * vehicle-accident
  */

    path: 'type/:id',
    component: QueryByTypeComponent,
  },

];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class QueryRoutingModule {}
